  const [selectedLanguage, setSelectedLanguage] = useState(
    props.selectedQuestion.question[0].language
  );

  const filterLanguages = () => {
    let langs: {
      key: QuestionLanguage;
      value: string;
    }[] = [];
    for (let langItem of props.selectedQuestion.question) {
      let testLangQ = QuestionLanguages.find(
        (l) => l.key === langItem.language
      );
      if (testLangQ !== undefined) {
        langs = [...langs, testLangQ];
      }
    }
    return langs;
  };

  useEffect(() => {
    if (
      props.questionTypes.questionTypes === null ||
      props.questionTypes.questionTypes.length === 0
    ) {
      props.loadQuestionTypes(setLoading);
    }
  }, [props]);
