const mapStateToProps = ({
  auth,
  doExam,
  candidateExam,
  system,
  questionTypes,
}: StoreState): {
  auth: Auth;
  doExam: examDataInterface;
  candidateExam: candidateExamStoreInterface;
  system: System;
  questionTypes: questionTypesStoreInterface;
} => {
  return {
    auth: auth,
    doExam: doExam,
    candidateExam: candidateExam,
    system: system,
    questionTypes: questionTypes,
  };
};

const QuestionPreview = connect(mapStateToProps, {
  setSelectedExamQuestion,
  setQuestionLanguage,
  loadQuestionTypes,
})(_QuestionPreview);

export default QuestionPreview;
