import React, { useEffect } from "react";
import { ImCheckboxChecked, ImCheckboxUnchecked } from "react-icons/im";
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import { QuestionAnswersStore } from "../../../../actions";
import {
  MultipleChoiceQuestionAnswerInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
  QuestionLanguage,
  SubQuestionInterface,
} from "../../../../interfaces/question";
import {
  questionTypeElementsType,
  QuestionForHybrid,
} from "@mastrecruit/common";
import Hybrid from "./Hybrid";
import TrueFalse from "./TrueFalse";
import {
  toCalculatedMultipleChoice,
  toEssay,
  toMatching,
  toMultipleChoice,
  toNumerical,
  toShortAnswer,
  toSimpleCalculated,
  toTrueFalse,
} from "../ConvertingAnswers";
import SimpleCalculated from "./SimpleCalculated";
import MultipleChoice from "./MultipleChoice";
import CalculatedMultipleChoice from "./CalculatedMultipleChoice";
import Matching from "./Matching";
import { Shuffle } from "../../../../utils/functions";
import { CandidateExam } from "../../../../interfaces/exam";
import Essay from "./Essay";
import Numerical from "./Numerical";
import ShortAnswer from "./ShortAnswer";

// All question types except Embedded and Hybrid

interface AnswersTypesCollectionProps {
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null;
    question: QuestionItemInterface | undefined;
    answers: QuestionAnswerInterface[] | undefined;
  };
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
  wholeQuestion: QuestionItemInterface | undefined;
  selectedExam: CandidateExam | null;
  selectedLanguage: QuestionLanguage;
  item: SubQuestionInterface;
  hybrid: boolean;
}

const AnswersTypesCollection = (props: AnswersTypesCollectionProps) => {
  return (
    <div>
      {/* TrueFalse */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.item.typeShort === QuestionForHybrid.TrueFalse &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <TrueFalse
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toTrueFalse(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}
      {/* Calculated */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        (props.item.typeShort === QuestionForHybrid.Calculated ||
          props.item.typeShort === QuestionForHybrid.SimpleCalculate) &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <SimpleCalculated
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toSimpleCalculated(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}
      {/* MultipleChoice */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        (props.item.typeShort === QuestionForHybrid.SingleMultiple ||
          props.item.typeShort === QuestionForHybrid.MultipleChoice) &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <MultipleChoice
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toMultipleChoice(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}
      {/* Calculated MultipleChoice */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.item.typeShort === QuestionForHybrid.CalculatedMultipleChoice &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <CalculatedMultipleChoice
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toCalculatedMultipleChoice(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}

      {/* Matching */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.item.typeShort === QuestionForHybrid.Matching &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <Matching
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toMatching(itm.answer),
                shuffledAnswers: Shuffle(toMatching(itm.answer)),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
              selectedExam={props.selectedExam}
              selectedLanguage={props.selectedLanguage}
            />
          ))}
      {/* Esssay */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.item.typeShort === QuestionForHybrid.Essay &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <Essay
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toEssay(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}
      {/* Numerical */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.item.typeShort === QuestionForHybrid.Numeric &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <Numerical
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toNumerical(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}
      {/* ShortAnswer */}
      {props.wholeQuestion !== undefined &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.item.typeShort === QuestionForHybrid.ShortAnswer &&
        props.selectedQuestionAnswer.answers
          .filter((test) => test.reference === props.item.reference)
          .map((itm, i) => (
            <ShortAnswer
              key={i + 1}
              type={props.type}
              available_answers={props.available_answers}
              selectedQuestionAnswer={{
                question_id: props.selectedQuestionAnswer.question_id,
                question: props.selectedQuestionAnswer.question,
                answers: toShortAnswer(itm.answer),
                selectedAns: itm,
              }}
              answerQuestionFn={props.answerQuestionFn}
            />
          ))}
    </div>
  );
};

export default AnswersTypesCollection;
