import React, { useEffect, useState } from "react";
import { BsFillCaretRightFill } from "react-icons/bs";
import { QuestionAnswersStore } from "../../../../actions";
import { CandidateExam } from "../../../../interfaces/exam";
import {
  MatchingQuestionAnswerInterface,
  MatchingStatuses,
  QuestionAnswerInterface,
  QuestionInterface,
  QuestionItemInterface,
  QuestionLanguage,
  QuestionLanguages,
} from "../../../../interfaces/question";
import MatchingSelect from "./MatchingSelect";
import { questionTypeElementsType } from "@mastrecruit/common";

interface MatchingProps {
  selectedExam: CandidateExam | null;
  selectedLanguage: QuestionLanguage;
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null;
    question: QuestionItemInterface | undefined;
    answers: MatchingQuestionAnswerInterface[] | undefined;
    shuffledAnswers: MatchingQuestionAnswerInterface[] | undefined;
    selectedAns: QuestionAnswerInterface;
  };
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
}

const Matching = (props: MatchingProps) => {
  const [displayWindow, setDisplayWindow] = useState(false);
  const [selectedAnswer, setSelectedAnswer] =
    useState<MatchingQuestionAnswerInterface | null>(null);
  const addAnswerItemFn = (
    newAnswers: MatchingQuestionAnswerInterface[],
    questionAnswer: QuestionAnswerInterface
  ) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.selectedQuestionAnswer.question &&
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: false,
          answers: [
            {
              _id: questionAnswer._id,
              reference: questionAnswer.reference,
              answer: newAnswers,
            },
          ],
          language: props.selectedQuestionAnswer.question.language,
        });
    }
  };

  const checkAnswer = (item: MatchingQuestionAnswerInterface): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
      if (check !== undefined) {
        let checkA = check.answers.find(
          (tes) =>
            tes.reference === props.selectedQuestionAnswer.selectedAns.reference
        );
        if (checkA !== undefined) {
          let x = checkA.answer.find((itm) => itm.id === item.id);
          if (x !== undefined && x.status !== "") {
            res = true;
          }
        }
      }
    }
    return res;
  };

  const addAnswer = (
    item: MatchingQuestionAnswerInterface,
    question_id: string | null
  ) => {
    let oldAnswers = props.available_answers.find(
      (itm) => itm.question_id === question_id
    );
    if (oldAnswers !== undefined) {
      let selAns = oldAnswers.answers.find(
        (tester) =>
          tester.reference ===
          props.selectedQuestionAnswer.selectedAns.reference
      );
      if (selAns !== undefined) {
        let newAnswers: MatchingQuestionAnswerInterface[] = [];
        for (let x of selAns.answer) {
          if (x.id === item.id) {
            newAnswers = [
              ...newAnswers,
              {
                id: item.id,
                value: item.value,
                status: item.status,
              },
            ];
          } else {
            newAnswers = [...newAnswers, x];
          }
        }
        addAnswerItemFn(newAnswers, selAns);
      }
    }
  };

  useEffect(() => {
    let test = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (
      test === undefined ||
      (test !== undefined && test.answers[0].answer.length === 0)
    ) {
      if (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer !== undefined &&
        props.selectedQuestionAnswer.question_id !== null
      ) {
        let newAnswers: MatchingQuestionAnswerInterface[] = [];
        for (let a of props.selectedQuestionAnswer.answers) {
          newAnswers = [
            ...newAnswers,
            {
              id: a.id,
              value: a.value,
              status: "",
            },
          ];
        }
        props.selectedQuestionAnswer.question !== undefined &&
          props.selectedQuestionAnswer.question &&
          props.answerQuestionFn({
            question_id: props.selectedQuestionAnswer.question_id,
            status: true,
            answers: [
              {
                _id: props.selectedQuestionAnswer.selectedAns._id,
                reference: props.selectedQuestionAnswer.selectedAns.reference,
                answer: newAnswers,
              },
            ],
            language: props.selectedQuestionAnswer.question.language,
          });
      }
    }
  }, [props]);

  const checkAnswerStatus = (
    selectedQuestionId: string,
    selectedLang: QuestionLanguage,
    ansi: MatchingQuestionAnswerInterface
  ) => {
    if (props.selectedExam !== null) {
      let testing = props.selectedExam.questions.find(
        (test) => test.question_id === selectedQuestionId
      );
      if (testing !== undefined) {
        for (let lang of QuestionLanguages) {
          let testLang = testing.question.find(
            (itm) => itm.language === lang.key
          );
          if (testLang !== undefined) {
            let checkE = testLang.answers.find(
              (tes) =>
                tes.reference ===
                props.selectedQuestionAnswer.selectedAns.reference
            );
            if (checkE !== undefined) {
              let testAnswerLang = checkE.answer.find(
                (it) => it.status.toString() === ansi.status
              );
              if (testAnswerLang !== undefined) {
                return testAnswerLang.status.toString();
              }
            }
          }
        }
      }
    }
    return false;
  };

  const checkIfSelected = (x: string) => {
    let temp = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (temp !== undefined) {
      let selAns = temp.answers.find(
        (tester) =>
          tester.reference ===
          props.selectedQuestionAnswer.selectedAns.reference
      );
      if (selAns !== undefined) {
        let ans = selAns.answer.find((itm) => itm.id === x);
        if (ans !== undefined && ans.status !== "") {
          if (props.selectedQuestionAnswer.question_id !== null) {
            return checkAnswerStatus(
              props.selectedQuestionAnswer.question_id,
              props.selectedLanguage,
              ans
            );
          }
        }
      }
    }
    return false;
  };

  const ReturnMatchingStatus = (): MatchingStatuses[] => {
    let res: MatchingStatuses[] = [];
    let question: QuestionInterface | undefined =
      props.selectedExam?.questions.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
    if (question !== undefined) {
      for (let item of question.question) {
        let selAns = item.answers.find(
          (tester) =>
            tester.reference ===
            props.selectedQuestionAnswer.selectedAns.reference
        );
        if (selAns !== undefined) {
          for (let ans of selAns.answer) {
            res = [
              ...res,
              {
                id: ans.id,
                lang: item.language,
                status: ans.status.toString(),
              },
            ];
          }
        } else {
          break;
        }
      }
    }
    return res;
  };

  const findWord = (lang: QuestionLanguage, status: string | false) => {
    let res: string = "";
    let arr: MatchingStatuses[] = ReturnMatchingStatus();
    if (arr.length > 0) {
      let item = arr.find((itm) => itm.status === status)?.id;
      if (item !== undefined) {
        let newItem = arr.find(
          (itm) => itm.id === item && itm.lang === lang
        )?.status;
        if (newItem !== undefined) {
          res = newItem;
        }
      }
    }
    return res;
  };

  const translateClass = " transform rotate-90";

  return (
    <div className="">
      {props.selectedQuestionAnswer !== null &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer.answers.map((item, i) => (
          <div
            key={i + 1}
            className={`${
              checkAnswer(item) === true
                ? "px-2 mb-2 py-2 md:py-0"
                : "px-2 py-2"
            } ${checkAnswer(item) === true ? "border rounded" : "border-b"} ${
              checkAnswer(item) === true ? "text-blue-600 border-blue-200" : ""
            } grid grid-cols-12`}
          >
            <div
              className={`col-span-12 ${
                checkAnswer(item) === true ? "md:col-span-6" : "md:col-span-8"
              } flex flex-row items-center gap-2`}
            >
              {checkAnswer(item) === true ? (
                <div className="flex items-center justify-center w-8 h-8 bg-blue-100 text-blue-600 text-xl font-normal rounded-full">
                  {i + 1}
                </div>
              ) : (
                <div className="flex items-center justify-center w-8 h-8 bg-gray-100 text-gray-500 text-xl font-normal rounded-full">
                  {i + 1}
                </div>
              )}
              <div
                dangerouslySetInnerHTML={{ __html: item.value }}
                className={`ml-2 ${
                  selectedAnswer !== null &&
                  selectedAnswer.id === item.id &&
                  displayWindow === true
                    ? "text-blue-600 font-extrabold"
                    : "text-gray-600"
                }`}
              ></div>
            </div>
            {checkAnswer(item) === true ? (
              <div
                title="Click to change selected choice"
                onClick={() => {
                  setSelectedAnswer(item);
                  setDisplayWindow(!displayWindow);
                }}
                className="flex flex-row items-center gap-2 col-span-12 md:col-span-6 cursor-pointer md:hover:bg-blue-50 py-4 pr-2 -mr-2 rounded-r md:pl-4 md:border-l border-blue-200"
              >
                <BsFillCaretRightFill
                  className={`text-xl transition transform${
                    selectedAnswer !== null &&
                    selectedAnswer.id === item.id &&
                    displayWindow === true
                      ? translateClass
                      : ""
                  }`}
                />
                <div
                  dangerouslySetInnerHTML={{
                    __html: findWord(
                      props.selectedLanguage,
                      checkIfSelected(item.id)
                    ),
                  }}
                ></div>
              </div>
            ) : (
              <div className="col-span-12 md:col-span-4 flex items-center md:justify-start mt-2 md:mt-0">
                <div
                  onClick={() => {
                    setSelectedAnswer(item);
                    setDisplayWindow(!displayWindow);
                  }}
                  title={
                    checkAnswer(item) === true
                      ? "Click to change selected choice"
                      : "Click to choose correct matching"
                  }
                  className={`btn text-sm ${
                    checkAnswer(item) === true ||
                    (selectedAnswer !== null &&
                      selectedAnswer.id === item.id &&
                      displayWindow === true)
                      ? "bg-blue-50"
                      : "bg-yellow-100 hover:bg-yellow-200"
                  } text-center cursor-pointer`}
                >
                  {checkAnswer(item) === true
                    ? findWord(props.selectedLanguage, checkIfSelected(item.id))
                    : selectedAnswer !== null &&
                      selectedAnswer.id === item.id &&
                      displayWindow === true
                    ? "Close matching list"
                    : "Choose matching"}
                </div>
              </div>
            )}
            <div className="col-span-12 md:col-span-12">
              {props.selectedQuestionAnswer.question_id !== null &&
                props.selectedQuestionAnswer.answers !== undefined &&
                props.selectedQuestionAnswer.shuffledAnswers !== undefined &&
                selectedAnswer !== null &&
                displayWindow === true &&
                selectedAnswer.id === item.id && (
                  <MatchingSelect
                    selectedAnswer={selectedAnswer}
                    selectedStatus={null}
                    selectedQuestionAnswer={{
                      question_id: props.selectedQuestionAnswer.question_id,
                      answers: props.selectedQuestionAnswer.answers,
                      shuffledAnswers:
                        props.selectedQuestionAnswer.shuffledAnswers,
                    }}
                    checkAnswer={checkAnswer}
                    addAnswer={addAnswer}
                    closeWindow={setDisplayWindow}
                    findWord={findWord}
                    selectedLanguage={props.selectedLanguage}
                    checkIfSelected={checkIfSelected}
                  />
                )}
            </div>
          </div>
        ))}
    </div>
  );
};

export default Matching;
