import React from "react";
import { MdArrowBack, MdRadioButtonUnchecked } from "react-icons/md";
import { RiCheckboxCircleFill } from "react-icons/ri";
import {
  MatchingQuestionAnswerInterface,
  QuestionLanguage,
} from "../../../../interfaces";

interface MatchingSelectProps {
  selectedAnswer: MatchingQuestionAnswerInterface;
  selectedStatus: string | null;
  selectedQuestionAnswer: {
    question_id: string;
    answers: MatchingQuestionAnswerInterface[];
    shuffledAnswers: MatchingQuestionAnswerInterface[];
  };
  checkAnswer: (item: MatchingQuestionAnswerInterface) => boolean;
  addAnswer: (
    item: MatchingQuestionAnswerInterface,
    question_id: string | null
  ) => void;
  closeWindow: (status: boolean) => void;
  findWord: (lang: QuestionLanguage, status: string | false) => string;
  selectedLanguage: QuestionLanguage;
  checkIfSelected: (x: string) => any;
}

const MatchingSelect = (props: MatchingSelectProps) => {
  return (
    // <div className="">
    <div className="bg-gray-200 rounded-b animate__animated animate__fadeInUp animate__faster -mx-2 mt-2">
      <div className="text-lg font-bold px-3 pt-2 flex flex-row items-center justify-between">
        <div className="flex flex-row items-center gap-3">
          <div
            title="Click to close this window"
            onClick={() => props.closeWindow(false)}
            className="h-10 w-10 text-center bg-gray-50 border border-white hover:border-blue-400 text-gray-900 rounded-full flex items-center justify-center cursor-pointer hover:bg-blue-100 hover:text-blue-700"
          >
            <MdArrowBack className="text-2xl" />
          </div>
          <span className="text-gray-800">Choose correct matching</span>
        </div>
      </div>
      <div className="bg-gray-200 p-2 mx-0">
        {props.selectedQuestionAnswer.shuffledAnswers.map((item, i) => (
          <div
            key={i + 1}
            onClick={() => {
              props.addAnswer(
                {
                  id: props.selectedAnswer.id,
                  value: props.selectedAnswer.value,
                  status: item.status,
                },
                props.selectedQuestionAnswer.question_id
              );
              props.closeWindow(false);
            }}
            className={`${
              item.status ===
              props.findWord(
                props.selectedLanguage,
                props.checkIfSelected(props.selectedAnswer.id)
              )
                ? "border-2 border-white bg-blue-100 font-bold text-blue-700"
                : "border text-gray-500 hover:text-gray-900 hover:font-extrabold"
            } cursor-pointer rounded bg-white hover:bg-blue-50 hover:border-white p-3 mb-2 flex flex-row items-center gap-3`}
          >
            <div>
              {item.status ===
              props.findWord(
                props.selectedLanguage,
                props.checkIfSelected(props.selectedAnswer.id)
              ) ? (
                <RiCheckboxCircleFill className="text-3xl text-blue-600" />
              ) : (
                <MdRadioButtonUnchecked className="text-3xl" />
              )}
            </div>
            <div dangerouslySetInnerHTML={{ __html: item.status }}></div>
          </div>
        ))}
      </div>
    </div>
    // </div>
  );
};

export default MatchingSelect;
