import React, { useEffect } from "react";
import { ImCheckboxChecked, ImCheckboxUnchecked } from "react-icons/im";
import { MdRadioButtonChecked, MdRadioButtonUnchecked } from "react-icons/md";
import { QuestionAnswersStore } from "../../../../actions";
import {
  CalculatedMultipleChoiceQuestionAnswerInterface,
  QuestionAnswerInterface,
  QuestionItemInterface,
} from "../../../../interfaces/question";
import { getRandomArbitrary } from "../../utils/utils";
import { questionTypeElementsType } from "@mastrecruit/common";

interface CalculatedMultipleChoiceProps {
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null;
    question: QuestionItemInterface | undefined;
    answers: CalculatedMultipleChoiceQuestionAnswerInterface[] | undefined;
    selectedAns: QuestionAnswerInterface;
  };
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
}

const CalculatedMultipleChoice = (props: CalculatedMultipleChoiceProps) => {
  const checkAnswer = (
    item: CalculatedMultipleChoiceQuestionAnswerInterface
  ): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
      if (check !== undefined) {
        let checkA = check.answers.find(
          (tes) =>
            tes.reference === props.selectedQuestionAnswer.selectedAns.reference
        );
        if (checkA !== undefined) {
          let x = checkA.answer.find((itm) => itm.id === item.id);
          if (x !== undefined && x.status === true) {
            res = true;
          }
        }
      }
    }
    return res;
  };

  const addAnswerItemFn = (
    newAnswers: CalculatedMultipleChoiceQuestionAnswerInterface[],
    questionAnswer: QuestionAnswerInterface
  ) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.selectedQuestionAnswer.question &&
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: false,
          answers: [
            {
              _id: questionAnswer._id,
              reference: questionAnswer.reference,
              answer: newAnswers,
            },
          ],
          language: props.selectedQuestionAnswer.question.language,
        });
    }
  };
  const addAnswer = (
    item: CalculatedMultipleChoiceQuestionAnswerInterface,
    question_id: string | null
  ) => {
    let oldAnswers = props.available_answers.find(
      (itm) => itm.question_id === question_id
    );
    if (oldAnswers !== undefined) {
      // Check type of question
      let temp: CalculatedMultipleChoiceQuestionAnswerInterface[] = [];
      if (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer.answers.filter(
          (itm) => itm.status === true
        ).length === 1
      ) {
        for (let t of props.selectedQuestionAnswer.selectedAns.answer) {
          temp = [
            ...temp,
            {
              id: t.id,
              value: t.value,
              status: false,
            },
          ];
        }
        addAnswerItemFn(temp, props.selectedQuestionAnswer.selectedAns);
      }
      let newAnswers: CalculatedMultipleChoiceQuestionAnswerInterface[] = [];
      for (let x of temp.length === 0
        ? props.selectedQuestionAnswer.selectedAns.answer
        : temp) {
        if (x.id === item.id) {
          newAnswers = [
            ...newAnswers,
            {
              id: item.id,
              value: item.value,
              status:
                props.selectedQuestionAnswer.answers !== undefined &&
                props.selectedQuestionAnswer.answers.filter(
                  (itm) => itm.status === true
                ).length === 1
                  ? true
                  : !x.status,
            },
          ];
        } else {
          newAnswers = [...newAnswers, x];
        }
      }
      addAnswerItemFn(newAnswers, props.selectedQuestionAnswer.selectedAns);
    }
  };
  useEffect(() => {
    let test = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (
      test === undefined ||
      (test !== undefined && test.answers[0].answer.length === 0)
    ) {
      if (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer !== undefined &&
        props.selectedQuestionAnswer.question_id !== null
      ) {
        let newAnswers: CalculatedMultipleChoiceQuestionAnswerInterface[] = [];
        for (let a of props.selectedQuestionAnswer.answers) {
          newAnswers = [
            ...newAnswers,
            {
              id: a.id,
              value: a.value,
              status: false,
            },
          ];
        }
        props.selectedQuestionAnswer.question !== undefined &&
          props.selectedQuestionAnswer.question &&
          props.answerQuestionFn({
            question_id: props.selectedQuestionAnswer.question_id,
            status: true,
            answers: [
              {
                _id: props.selectedQuestionAnswer.selectedAns._id,
                reference: props.selectedQuestionAnswer.selectedAns.reference,
                answer: newAnswers,
              },
            ],
            language: props.selectedQuestionAnswer.question.language,
          });
      }
    }
  }, [props]);
  return (
    <div>
      {props.selectedQuestionAnswer !== null &&
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer.answers.map((item, i) => (
          <div
            key={i + 1}
            onClick={() =>
              addAnswer(item, props.selectedQuestionAnswer.question_id)
            }
            className={`px-2 py-2 border-b ${
              checkAnswer(item) === true
                ? "border bg-blue-100 border-white text-blue-600 hover:border-blue-600 rounded font-bold"
                : "border-b bg-white border-gray-100 hover:bg-gray-100"
            } flex flex-row justify-left items-center rounded cursor-pointer`}
          >
            {checkAnswer(item) === true ? (
              <>
                {props.selectedQuestionAnswer.answers !== undefined &&
                props.selectedQuestionAnswer.answers.filter(
                  (itm) => itm.status === true
                ).length === 1 ? (
                  <MdRadioButtonChecked className="text-2xl" />
                ) : (
                  <ImCheckboxChecked className="text-2xl" />
                )}
              </>
            ) : props.selectedQuestionAnswer.answers !== undefined &&
              props.selectedQuestionAnswer.answers.filter(
                (itm) => itm.status === true
              ).length === 1 ? (
              <MdRadioButtonUnchecked className="text-2xl" />
            ) : (
              <ImCheckboxUnchecked className="text-2xl" />
            )}
            <span className="ml-2">
              {item.value.length > 2
                ? item.value
                : item.value.length === 2
                ? getRandomArbitrary(item.value[0], item.value[1])
                    .toFixed(2)
                    .toString()
                : item.value[0]}
            </span>
          </div>
        ))}
    </div>
  );
};

export default CalculatedMultipleChoice;
