import React, { useEffect } from "react";
import { QuestionAnswersStore } from "../../../../actions";
import {
  QuestionAnswerInterface,
  QuestionItemInterface,
  ShortAnswerQuestionAnswerInterface,
} from "../../../../interfaces/question";
import { questionTypeElementsType } from "@mastrecruit/common";

interface ShortAnswerProps {
  type: questionTypeElementsType | null;
  available_answers: QuestionAnswersStore[];
  selectedQuestionAnswer: {
    question_id: string | null;
    question: QuestionItemInterface | undefined;
    answers: ShortAnswerQuestionAnswerInterface[] | undefined;
    selectedAns: QuestionAnswerInterface;
  };
  answerQuestionFn: (answer: QuestionAnswersStore) => void;
}

const ShortAnswer = (props: ShortAnswerProps) => {
  const checkAnswer = (item: ShortAnswerQuestionAnswerInterface): boolean => {
    let res = false;
    if (props.available_answers.length > 0) {
      let check = props.available_answers.find(
        (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
      );
      if (check !== undefined) {
        let checkA = check.answers.find(
          (tes) =>
            tes.reference === props.selectedQuestionAnswer.selectedAns.reference
        );
        if (checkA !== undefined) {
          let x = checkA.answer.find((itm) => itm.id === item.id);
          if (x !== undefined && x.value !== "") {
            res = true;
          }
        }
      }
    }
    return res;
  };
  const addAnswerItemFn = (
    newAnswers: ShortAnswerQuestionAnswerInterface[],
    questionAnswer: QuestionAnswerInterface
  ) => {
    if (props.selectedQuestionAnswer.question_id !== null) {
      props.selectedQuestionAnswer.question &&
        props.answerQuestionFn({
          question_id: props.selectedQuestionAnswer.question_id,
          status: false,
          answers: [
            {
              _id: questionAnswer._id,
              reference: questionAnswer.reference,
              answer: newAnswers,
            },
          ],
          language: props.selectedQuestionAnswer.question.language,
        });
    }
  };
  const addAnswer = (
    item: { id: string; value: string; status: boolean },
    question_id: string | null
  ) => {
    let oldAnswers = props.available_answers.find(
      (itm) => itm.question_id === question_id
    );
    if (oldAnswers !== undefined) {
      // Check type of question
      addAnswerItemFn(
        [
          {
            id: props.selectedQuestionAnswer.selectedAns.answer[0].id,
            value: item.value,
            status: props.selectedQuestionAnswer.selectedAns.answer[0].status,
          },
        ],
        props.selectedQuestionAnswer.selectedAns
      );
    }
  };
  const answeredValue = () => {
    let answer = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    let checkB: QuestionAnswerInterface | undefined = undefined;
    checkB =
      answer === undefined
        ? undefined
        : answer.answers.find(
            (tes) =>
              tes.reference ===
              props.selectedQuestionAnswer.selectedAns.reference
          );
    return answer !== undefined && checkB !== undefined
      ? checkB.answer.length !== 0 && checkB.answer[0].value
      : "";
  };

  useEffect(() => {
    let test = props.available_answers.find(
      (itm) => itm.question_id === props.selectedQuestionAnswer.question_id
    );
    if (
      test === undefined ||
      (test !== undefined && test.answers[0].answer.length === 0)
    ) {
      if (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer !== undefined &&
        props.selectedQuestionAnswer.question_id !== null
      ) {
        props.selectedQuestionAnswer.question !== undefined &&
          props.selectedQuestionAnswer.question &&
          props.answerQuestionFn({
            question_id: props.selectedQuestionAnswer.question_id,
            status: true,
            answers: [
              {
                _id: props.selectedQuestionAnswer.selectedAns._id,
                reference: props.selectedQuestionAnswer.selectedAns.reference,
                answer: [
                  {
                    id: props.selectedQuestionAnswer.answers[0].id,
                    value: "",
                    status: props.selectedQuestionAnswer.answers[0].status,
                  },
                ],
              },
            ],
            language: props.selectedQuestionAnswer.question.language,
          });
      }
    }
  }, [props]);
  return (
    <div className="">
      {props.selectedQuestionAnswer === undefined ? (
        <div
          className={`-mx-2 -my-2 flex flex-col justify-left rounded cursor-pointer`}
        >
          <textarea
            value={""}
            className={`px-3 py-2 rounded focus:outline-none focus:border-none border text-gray-700 border-gray-500 bg-gray-50`}
            style={{ minHeight: "80px" }}
          ></textarea>
        </div>
      ) : (
        props.selectedQuestionAnswer.answers !== undefined &&
        props.selectedQuestionAnswer.answers.map((item, i) => (
          <div
            key={i + 1}
            className={`-mx-2 -my-2 flex flex-col justify-left rounded cursor-pointer`}
          >
            <textarea
              onChange={(e) => {
                addAnswer(
                  {
                    id: item.id,
                    value: e.target.value,
                    status: item.status,
                  },
                  props.selectedQuestionAnswer.question_id
                );
              }}
              value={answeredValue()}
              className={`px-3 py-2 rounded focus:outline-none focus:border-none ${
                checkAnswer(item) === true
                  ? "border-2 text-blue-600 font-bold bg-white border-blue-600"
                  : "border text-gray-700 border-gray-500 bg-gray-50"
              }`}
              style={{ minHeight: "80px" }}
            ></textarea>
          </div>
        ))
      )}
    </div>
  );
};

export default ShortAnswer;
